import { CangjieDataTransfer, constants } from '@ali/4ever-cangjie';

const { CUSTOM_MIME_TYPES } = constants;
const customTypes = new Set(Object.values(CUSTOM_MIME_TYPES));

/**
 * @description 获取剪切板上自定义的 mime type 数据
 */
export default function getAllCustomClipboardData(
  dataTransfer: DataTransfer | CangjieDataTransfer | null,
): [string, string][] {
  if (!dataTransfer?.types) {
    return [];
  }
  const mimeTypes = dataTransfer.types as string[];
  return mimeTypes
    .filter((t) => customTypes.has(t))
    .map((type) => {
      return [type as string, dataTransfer?.getData(type) as string];
  });
}
